input[type=text]{
    width: 427px;     
    height: 68px;
    margin: 0;
    background-color: #F3F3F4;
    border-radius: 16px;
    outline: none;
    border: 0;
    font-style: normal;
    font-weight: 900;
    font-size: 30px;
    line-height: 36px;
    padding-left: 24px;      
};

input[type=text]:focus{
  border: 2px solid #F3F3F4;
  background-color: #fff;
}
input[type=text]:hover ~ input[type=range]::-webkit-slider-runnable-track{
  height: 1px;
}

input[type=text]:hover ~ input[type=range]::-moz-range-track {
  height: 1px;
}

input[type=text]:hover ~ input[type=range]::-ms-track {
  height: 1px;
}


input[type=text]:hover ~ input[type=range]::-webkit-slider-thumb {
  height: 24px;
  width: 24px;
  margin-top: -11px;
}

input[type=text]:hover ~ input[type=range]::-moz-range-thumb {
  height: 24px;
  width: 24px;
}

input[type=text]:hover ~ input[type=range]::-ms-thumb {
  height: 24px;
  width: 24px;
}


input[type=text]::-webkit-outer-spin-button,
input[type=text]::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

input[type=range]{
    -webkit-appearance: none;
    width: 90%;
    position: absolute;
    bottom: -2px;
    left: 22px;
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;  
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: #FF9514;
  cursor: pointer;
  margin-top: -9px; 
}


input[type=range]::-moz-range-thumb {  
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: #FF9514;
  cursor: pointer;
}


input[type=range]::-ms-thumb { 
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: #FF9514;
  cursor: pointer;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 2px;
  cursor: pointer; 
  background: #FF9514; 
}

input[type=range]:focus::-webkit-slider-runnable-track {
  background: #FF9514;
}

input[type=range]::-moz-range-track {
  width: 100%;
  height: 2px;
  cursor: pointer; 
  background: #FF9514; 
}

input[type=range]::-ms-track {
  width: 100%;
  height: 2px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  border-width: 16px 0;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: #FF9514; 
}

input[type=range]:focus::-ms-fill-lower {
  background: #FF9514;
}

input[type=range]::-ms-fill-upper {
  background: #FF9514; 
}

input[type=range]:focus::-ms-fill-upper {
  background: #FF9514;
}